@media (min-width: 768px) {
    img.logo {
        width: 85px;
        object-fit: cover;
        height: 85px;
        border-radius: 50%;
        border: 6px solid #000000;
    }

    nav {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 20px;
        width: 100%;

    }

    .link {
        font-size: 12px;
        color: black;
        text-decoration: none;
    }

}

@media (max-width: 768px) {
    img.logo {
        width: 60px;
        object-fit: cover;
        height: 60px;
        border-radius: 50%;
        border: 3px solid #000000;
    }

    nav {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 10px;
        width: 100%;

    }

    .link {
        font-size: 12px;
        color: black;
        text-decoration: none;
    }

}