@media (min-width: 768px) {
    div.post {
        max-width: 600px;
        margin-top: 0px;
        font-family: 'Roboto', sans-serif;
        background-color: white;
        padding: 50px;
    }

    div.Ingredients {
        display: flex;
        justify-content: space-between;
    }

    ul.macros,
    ul.Testimonies {
        list-style-type: none;
        padding: 0;
    }

    ul.faq {
        text-align: left;
        padding: 0;
    }

    ol.directions {
        text-align: left;
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    h1.HERO {
        font-size: 50px;
        text-decoration: underline;
    }
}

@media (max-width: 768px) {
    div.post {
        max-width: 280px;
        margin-top: 0px;
        font-family: 'Roboto', sans-serif;
        background-color: white;
        padding: 20px;
    }

    div.Ingredients {
        display: flex;
        justify-content: space-between;
    }

    ul.macros,
    ul.Testimonies {
        list-style-type: none;
        padding: 0;
    }

    ul.faq {
        text-align: left;
        padding-left: 20px;
    }



    ol.directions {
        text-align: left;
        display: flex;
        flex-direction: column;
        padding-left: 20px;
        gap: 12px;
    }


    h1.HERO {
        font-size: 30px;
        text-decoration: underline;
    }

    h1 {
        font-size: 20px;
    }

    h2 {
        font-size: 18px;
        margin-top: 40px;
        margin-bottom: 0px;
    }

    h3 {
        font-size: 16px;
    }

    p,
    li {
        font-size: 14px;
    }
}

ul.faq li {
    font-weight: bold;
}