div.IngredientPanel {
    display: flex;
    flex-direction: column;
    align-items: baseline;
    text-align: left;
}

ul.IngredientColumns {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    padding: 0px;
}

div.IngredientColumn {
    display: flex;
    flex-direction: column;
    align-items: baseline;
    text-align: left;

}

.IngredientColumn ul {
    padding-left: 20px;
}

div.multiplier {
    margin: 40px;
}